@font-face {
  font-family: "Etelka-regular";
  src: url("../../assets/fonts/EtelkaPro-Regular.woff2");
  src: url("../../assets/fonts/EtelkaPro-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Etelka-medium";
  src: url("../../assets/fonts/EtelkaPro-Medium.woff2");
  src: url("../../assets/fonts/EtelkaPro-Medium.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Etelka-light";
  src: url("../../assets/fonts/EtelkaPro-Light.woff2");
  src: url("../../assets/fonts/EtelkaPro-Light.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  color: inherit;
  background-color: inherit;
  border: none;
  cursor: pointer;
}

li {
  list-style: none;
}

.container {
  max-width: 1340px;
  margin: 0 auto;
}

body {
  font-family: "Etelka-regular";
  color: #071108;
}

@import-normalize;
.profile {
  padding-top: 60px;
  padding-bottom: 20px;
  margin-bottom: 30px;
}
.profile__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
}
.profile__image {
  width: 200px;
  height: 200px;
  background-color: grey;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.profile__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.profile__name {
  display: flex;
  column-gap: 10px;
  font-size: 25px;
}

.input-section {
  width: 33.3333333333%;
}
.input-section__container {
  width: 100%;
  border: 1px solid #071108;
  padding: 20px 20px 10px 20px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.input-section__title {
  font-size: 20px;
}
.input-section__form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.input-section__input-wrapper {
  width: 100%;
}
.input-section__input {
  width: 100% !important;
  height: 120px !important;
  resize: none;
  padding: 10px;
  font-family: "Etelka-regular";
  font-size: 16px;
  border-radius: 5px;
}
.input-section__submit-btn {
  align-self: flex-end;
  padding: 10px 15px;
  font-size: 14px;
  background-color: #B5BEC6;
  border-radius: 6px;
}
.input-section__submit-btn:hover {
  background-color: #BFB1C1;
}

.main__container {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.posts {
  width: 66.6666666667%;
}
.posts__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.posts .post {
  background-color: white;
  border: 1px solid #071108;
  border-radius: 5px;
  padding: 10px;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.posts .post__name {
  display: flex;
  gap: 10px;
  font-size: 18px;
  align-self: flex-end;
}